import React from "react"
import Link from "./Link"

export function SidebarLink({ href, children }) {
  return (
    <li className="mt-2">
      <Link href={href}>
        <a className="text-primary-600 hover:text-primary-800">{children}</a>
      </Link>
    </li>
  )
}

export function SidebarLinkList({ children }) {
  return <ul className="my-4">{children}</ul>
}

export default function SideBar({ children }) {
  return <section className="p-4">{children}</section>
}
